import { Checkbox } from '@mui/material';
import * as React from 'react';

export default function SelectionsComp(props) {
    console.log(props.obj.url);
    let [isSelected, setIsSelect] = React.useState(props.obj.remove);
    React.useEffect(()=>{
        props.obj.remove = isSelected;
        props.setimgToRemove(props.obj);
    },[isSelected]);
    return(<div className={`${isSelected ? "removeStyle" : ""}`}>
                <hr/><hr/>
                <Checkbox checked={isSelected} onChange={()=>{setIsSelect(!isSelected);}}></Checkbox>
                <br/>
                {(!props.isVid ? <img src={props.obj.url}  className="selImgRmv"/> : "")}
                {(props.isVid ? 
                <video width="320" height="240" controls>
                <source src={props.obj.url}  type="video/mp4"/>
                <source src={props.obj.url}  type="video/ogg"/>
                Your browser does not support the video tag.
              </video> : "")}
            </div>);
}