import React, { useCallback, useContext, useState } from "react";
import FirebaseContext from "./firebaseContext";
import { getDownloadURL, getStorage, ref, uploadBytesResumable } from "firebase/storage";


export default function useFbUpload() {
    const [progress, setProgress] = useState(0);
    const [file, setFile] = useState("");
    const [error, setError] = useState("");
    const fbCtxt = useContext(FirebaseContext);

    let uploadFileToFbStorage = useCallback((fileObj,foldername) => {
        const metadata = {
            contentType: fileObj.type
        };
        const storageRef = ref(fbCtxt.storage, foldername+'/' + "f"+new Date().getTime()+fileObj.name);
        const uploadTask = uploadBytesResumable(storageRef, fileObj, metadata);

        // Listen for state changes, errors, and completion of the upload.
        uploadTask.on('state_changed',
            (snapshot) => {
                // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
                const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                console.log('Upload is ' + progress + '% done');
                setProgress(progress);
                switch (snapshot.state) {
                    case 'paused':
                        console.log('Upload is paused');
                        break;
                    case 'running':
                        console.log('Upload is running');
                        break;
                }
            },
            (error) => {
                // A full list of error codes is available at
                // https://firebase.google.com/docs/storage/web/handle-errors
                switch (error.code) {
                    case 'storage/unauthorized':
                        // User doesn't have permission to access the object
                        setError("Permission Error - ",error.code);
                        break;
                    case 'storage/canceled':
                        // User canceled the upload
                        setError("Cancled By User - ",error.code);
                        break;

                    // ...

                    case 'storage/unknown':
                        // Unknown error occurred, inspect error.serverResponse
                        setError("Storage/Unknown Error - ",error.code);
                        break;
                }
                fbCtxt.setLoading(false);  
            },
            () => {
                // Upload completed successfully, now we can get the download URL
                getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
                    console.log('File available at', downloadURL);
                    setFile(downloadURL);
                    fbCtxt.setLoading(false);  
                });
            }
        );

        console.log(" FILE UPLOAD OBJ EXECUTION STARTED", fileObj, fbCtxt.storage);
    }, []);

    return ({
        file,
        error,
        progress,
        uploadFileToFbStorage,
        setFile
    });
}