import { Button } from "@mui/material";
import { useState } from "react";
import UploadOnDisplay from "./UploadOnDisplay";

export default function UpMultipleImages(props) {
    const [fileList, setFileList] = useState([]);
    let selectImage = (e) => {
        for (let i = 0; i < e.target.files.length; i++) {
            let file = e.target.files[i];
            setFileList((p) => { return ([...p, file]) });
        }
    }
    return (
        <span>
            <Button variant="contained" component="label" style={{ width: "90%" }}>
                Upload Images For Album
                <input hidden accept="image/*" type="file" multiple
                    onChange={selectImage}
                //   disabled={uploadState === 'uploading'}
                />
            </Button><br /><br />
            <ol>
                {fileList.map((v) => 
                    { 
                        return (
                            <li key={v.name+v.lastModified}>
                            <UploadOnDisplay typeOfAsset="img" currentComp={props.currentComp}  file={v} setPathToAssetInAlbum={props.setPathToImgInAlbum}></UploadOnDisplay>
                            </li>); 
                    })}
            </ol>
        </span>
    );
}