import { Button, Paper, TextField } from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import FirebaseContext from "../firebaseContext";
import useFbUpload from "../useFbUpload";
import UpMultipleImages from "./adminComps/UpMultipleImages";
import UpMultipleVideos from "./adminComps/UpMultipleVideos";
import SelectionsComp from "./components/SelectionsComp";

export default function AdminPage(props) {
    const [isError, setIsError] = useState();
    const [customUid, setcustomUid] = useState(new Date().getTime());

    let newImagesList = [];
    let newVideosList = [];

    let [rmvSelectionsImg,setRmvSelectionsImg] = useState([]);
    let [rmvSelectionsVid,setRmvSelectionsVid] = useState([]);

    let imgToRemove = [];

    const [dateVal, setDateVal] = useState("2022-01-01");
    const [name, setName] = useState("");
    const [image, setImage] = useState("");
    const [desc, setDesc] = useState("");
    const [owner, setOnwer] = useState("");
    const [sname, setSname] = useState("");
    const [saddress, setSaddress] = useState("");
    const [sweb, setSweb] = useState("");
    const [syoutube, setSyoutube] = useState("");
    const [syoutubeid, setSyoutubeid] = useState("");
    const [sinsta, setSinsta] = useState("");
    const [sinstaid, setSinstaid] = useState("");
    const [studioLogo, setstudioLogo] = useState("");
    const fbCtxt = useContext(FirebaseContext);

    const logoImg = useFbUpload();
    const studioLogoFb = useFbUpload();

    let clickedBtn = () => {
        let tmp = {
            customUid:customUid,
            name: name,
            date: dateVal,
            fullDesc: desc,
            image: image,
            instagram: sinsta,
            instagramId: sinstaid,
            owner: owner,
            studio: sname,
            studioDetails: saddress,
            website: sweb,
            youtube: syoutube,
            youtubeId: syoutubeid,
            studioLogo: studioLogo,
            userphone: [owner],
            users: [{
                message: "owner",
                phone: owner
            }],
            images: [],
            videos: []
        };
        console.log("Button Clicked = ", tmp);
        try {
            setIsError();
            if (!tmp.name.trim()) { setIsError(p => { return (p + ", Name is Empty, ") }) }
            if (!tmp.fullDesc.trim()) { setIsError(p => { return (p + "One Liner is Empty, ") }) }
            if (!tmp.image.trim()) { setIsError(p => { return (p + "Album Image is Empty, ") }) }
            if (!tmp.owner.trim()) { setIsError(p => { return (p + "Owner is Empty, ") }) }
            if (!tmp.studio.trim()) { setIsError(p => { return (p + "Studio Name is Empty, ") }) }
            if (!tmp.studioDetails.trim()) { setIsError(p => { return (p + "Studio Details is Empty, ") }) }
            if (!tmp.website.trim()) { setIsError(p => { return (p + "Website is Empty, ") }) }

            if (!tmp.name.trim() || !tmp.fullDesc.trim() || !tmp.image.trim()
                || !tmp.owner.trim() || !tmp.studio.trim() || !tmp.studioDetails.trim()
                || !tmp.website.trim()) {
                console.log("ERROR : ", isError);
                fbCtxt.setLoading(false);
            } else if(!props.modify){
                console.log("Value of ERROR (no errors :) )", isError);
                fbCtxt.setLoading(true);
                fbCtxt.addAlbum(tmp);
            }else if((props.currentComp && props.modify)){
                fbCtxt.setLoading(true);
                tmp.docId=props.currentComp.docId;
                tmp.date=props.currentComp.date;
                tmp.userphone=props.currentComp.userphone;
                tmp.users=props.currentComp.users;
                tmp.images=props.currentComp.images.concat(newImagesList);
                tmp.videos=props.currentComp.videos.concat(newVideosList);
                fbCtxt.saveEditedAlbum(tmp);
                props.setCurrentComp();
            }else{
                alert("Nothing to do...");
            }

        } catch (e) {
            console.log("ERROR : ", e);
            setIsError("Errored : ", e);
            fbCtxt.setLoading(false);
        }
    }

    let removeImages = () => {
        let tmp = {
            customUid:customUid,
            name: name,
            date: dateVal,
            fullDesc: desc,
            image: image,
            instagram: sinsta,
            instagramId: sinstaid,
            owner: owner,
            studio: sname,
            studioDetails: saddress,
            website: sweb,
            youtube: syoutube,
            youtubeId: syoutubeid,
            studioLogo: studioLogo,
            userphone: [owner],
            users: [{
                message: "owner",
                phone: owner
            }],
            images: [],
            videos: []
        };
        console.log("removeImages Button Clicked = ", tmp);
        if((props.currentComp && props.modify)){
            fbCtxt.setLoading(true);
            tmp.docId=props.currentComp.docId;
            tmp.date=props.currentComp.date;
            tmp.userphone=props.currentComp.userphone;
            tmp.users=props.currentComp.users;
            tmp.images=props.currentComp.images.concat(newImagesList);
            tmp.videos=props.currentComp.videos.concat(newVideosList);
            fbCtxt.saveEditedAlbum(tmp);
            props.setCurrentComp();
        
            tmp.images = tmp.images.filter(v=>{
                return(!imgToRemove.includes(v));
            });
            console.log("imgToRemove",tmp);
            fbCtxt.saveEditedAlbum(tmp); 
        }else{
            alert("Nothing to do...");
        }
    }

    let selectImage = (e) => {
        fbCtxt.setLoading(true);
        logoImg.uploadFileToFbStorage(e.target.files[0],"f_"+customUid);
    }
    let selectStudioLogo = (e) => {
        fbCtxt.setLoading(true);
        studioLogoFb.uploadFileToFbStorage(e.target.files[0],"f_"+customUid);
    }

    useEffect(() => {
        console.log("Album img Uploaded And Local Var is ready");
        setImage(logoImg.file)
    }, [logoImg.file])

    useEffect(() => {
        console.log("Studio Logo Uploaded And Local Var is ready");
        setstudioLogo(studioLogoFb.file)
    }, [studioLogoFb.file])

    useEffect(() => {
        if (props.modify) {
            console.log("MODE is MODIFY....",props);
            // setDateVal(props.currentComp.date);
            if (props.currentComp?.name) {
                setcustomUid(props.currentComp.customUid);
                setName(props.currentComp.name);
                setDesc(props.currentComp.fullDesc);
                setOnwer(props.currentComp.owner);
                setSname(props.currentComp.studio);
                setSaddress(props.currentComp.studioDetails);
                setSweb(props.currentComp.website);
                setSyoutube(props.currentComp.youtube);
                setSyoutubeid(props.currentComp.youtubeId);
                setSinsta(props.currentComp.instagram);
                setSinstaid(props.currentComp.instagramId);
                setstudioLogo(props.currentComp.studioLogo);
                logoImg.setFile(props.currentComp.image);
                studioLogoFb.setFile(props.currentComp.studioLogo);
            }
            let tmpRmvSelectionsImg = props.currentComp.images.map(v=>{return({url:v,remove:false});});
            setRmvSelectionsImg(tmpRmvSelectionsImg);
            let tmpRmvSelectionsVid = props.currentComp.videos.map(v=>{return({url:v,remove:false});});
            setRmvSelectionsVid(tmpRmvSelectionsVid);
        }
    }, [props.currentComp]);

    let setPathToImgInAlbum = (data)=>{
        console.log("Img to add DATA",data);
        newImagesList.push(data);
    }
    
    let setPathToVidInAlbum = (data)=>{
        console.log("Vid to add DATA",data);
        newVideosList.push(data);
    }

    let setimgToRemove = (obj)=>{
        if(imgToRemove.includes(obj.url) && !obj.remove){
            imgToRemove = imgToRemove.filter((v)=>{
                if(v==obj.url){return false;}
            });
        }else if(!imgToRemove.includes(obj.url) && obj.remove){
            imgToRemove.push(obj.url);
        }
    }

    return (<Paper elevation={0} style={{ textAlign: "center", marginTop: "5%" }}>
        <h3>{!props.modify ? "Add New Album" : "Modify Album"}</h3>
        <TextField id="outlined-basic" label="Album Name" variant="outlined" className="width90per" value={name} onChange={(e) => { setName(e.target.value) }} />
        {/* <br/><br/>
    <TextField id="outlined-basic" label="Album Image" variant="outlined" className="width90per" 
    type={"file"}
    value={image} onChange={(e)=>{setImage(e.target.value)}}/> */}
        <br /><br />
        <Button variant="contained" component="label" style={{ width: "90%" }}>
            Upload Album Img
            <input hidden accept="image/*" type="file"
                onChange={selectImage}
            //   disabled={uploadState === 'uploading'}
            />
        </Button>
        {(logoImg.error && 
            <h5 style={{ color: "red" }}>{logoImg.error}</h5>
        )}
        {(logoImg.file && 
            <h5 style={{ color: "green" }}>{logoImg.file}<img alt="no img" width="60%" src={logoImg.file}/></h5>
        )}
        <br /><br />
        <TextField id="outlined-basic" label="Album one liner" variant="outlined" className="width90per" value={desc} onChange={(e) => { setDesc(e.target.value) }} />
        <br /><br />
       {!props.modify && <TextField id="outlined-basic" label="Album Date" type="date" value={dateVal} onChange={(e) => { setDateVal(e.target.value) }} variant="outlined" className="width90per" />}
        <br /><br />
        <TextField id="outlined-basic" label="Owner Number" variant="outlined" className="width90per" value={owner} onChange={(e) => { setOnwer(e.target.value) }} />
        <br /><br />
        <TextField id="outlined-basic" label="Studio Name" variant="outlined" className="width90per" value={sname} onChange={(e) => { setSname(e.target.value) }} />
        <br /><br />
        <TextField id="outlined-basic" label="Studio Address" variant="outlined" className="width90per" value={saddress} onChange={(e) => { setSaddress(e.target.value) }} />
        <br /><br />
        <Button variant="contained" component="label" style={{ width: "90%" }}>
            Upload Studio Logo
            <input hidden accept="image/*" type="file"
                onChange={selectStudioLogo}
            //   disabled={uploadState === 'uploading'}
            />
        </Button>
        {(studioLogoFb.error && 
            <h5 style={{ color: "red" }}>{studioLogoFb.error}</h5>
        )}
        {(studioLogoFb.file && 
            <h5 style={{ color: "green" }}>
            {studioLogoFb.file}<img alt="no img" width="60%" src={studioLogoFb.file}/>
            </h5>
        )}
        <br /><br />

        <TextField id="outlined-basic" label="Studio Website" variant="outlined" className="width90per" value={sweb} onChange={(e) => { setSweb(e.target.value) }} />
        <br /><br />
        <TextField id="outlined-basic" label="Studio youtube link" variant="outlined" className="width90per" value={syoutube} onChange={(e) => { setSyoutube(e.target.value) }} />
        <br /><br />
        <TextField id="outlined-basic" label="Studio youtube id" variant="outlined" className="width90per" value={syoutubeid} onChange={(e) => { setSyoutubeid(e.target.value) }} />
        <br /><br />
        <TextField id="outlined-basic" label="Studio instagram link" variant="outlined" className="width90per" value={sinsta} onChange={(e) => { setSinsta(e.target.value) }} />
        <br /><br />
        <TextField id="outlined-basic" label="Studio instagram id" variant="outlined" className="width90per" value={sinstaid} onChange={(e) => { setSinstaid(e.target.value) }} />
        <br /><br />
        {isError && <h5 style={{ color: "red" }}>{isError}</h5>}
        <Button variant="contained" onClick={clickedBtn}>Submit</Button>
        <br/><br/><br/>
        {props.modify ? 
            <div>
                <Button variant="contained" onClick={()=>{props.setCurrentComp();}}>Back</Button>
                <br/><br/><br/>
                <UpMultipleImages currentComp={props.currentComp} setPathToImgInAlbum={setPathToImgInAlbum}></UpMultipleImages>
                <br /><br />
                <Button variant="contained" onClick={clickedBtn}>Submit Img To Save in Album</Button>
            </div>
            :
            ""
        }
        <br /><br /><br/>
        {props.modify ? 
            <div>
                <Button variant="contained" onClick={()=>{props.setCurrentComp();}}>Back</Button>
                <br/><br/><br/>
                <UpMultipleVideos currentComp={props.currentComp} setPathToVidInAlbum={setPathToVidInAlbum}></UpMultipleVideos>
                <br /><br />
                <Button variant="contained" onClick={clickedBtn}>Submit Vide To Save in Album</Button>
            </div>
            :
            ""
        }
        <br /><br /><br/>
        <hr/><hr/><hr/>
        <h2>Image - Remove</h2>
        <hr/><hr/><hr/>
        <br /><br /><br/>
        {rmvSelectionsImg.map((obj,index)=>{
            return(<SelectionsComp obj={obj} key={index} setimgToRemove={setimgToRemove} />);
        })}
        <br/>
        <Button variant="contained" onClick={removeImages}>Save Removed Images</Button>
        <br /><br /><br/>
        <hr/><hr/><hr/>
        <h2>Video - Remove</h2>
        <hr/><hr/><hr/>
        <br /><br /><br/>
        {rmvSelectionsVid.map((obj,index)=>{
            return(<SelectionsComp obj={obj} key={index} setimgToRemove={setimgToRemove} isVid={true} />);
        })}
        <br/>
        <Button variant="contained" onClick={removeImages}>Save Removed Videos</Button>
        <br /><br /><br/>
    </Paper>);
}