import { useEffect } from "react";
import useFbUpload from "../../useFbUpload";

export default function UploadOnDisplay(props){
    const uploadFileService = useFbUpload();
    useEffect(()=>{
        console.log(props.file.name);
        uploadFileService.uploadFileToFbStorage(props.file,"f_"+props.currentComp.customUid);
    },[]);
    useEffect(()=>{
        if(uploadFileService.file!==""){
            console.log("FILE UPLOADED ",uploadFileService.file);
                props.setPathToAssetInAlbum(uploadFileService.file);
        }
    },[uploadFileService.file]);
    return(<div>
        <h5>{props.file.name}
        <br/>
        {uploadFileService.error?<span color="red">{uploadFileService.error}</span>:""}
        <br/>
        {uploadFileService.file}
        <br/>
        <video src={uploadFileService.file} alt="inUpload" width="40%"></video>
        <br/>
        ({uploadFileService.progress}%)
        </h5>
        <hr/>
    </div>);
}